            reportLoading,
            checkedOneOnChange,
            checkedTowOnChange,
            cycleMonitoringBtnLoading,
            fundedBillingDialog,
            bloodBillingDialog,
            openPdfReport,
            isStyleRed,
            openEncounter,
            deassignCycle
        } = useDailyReportReportsInit();

        return {
            reportList,
            reportLoading,
            selectedReport,
            checkedOneOnChange,
            checkedTowOnChange,
            cycleMonitoringBtnLoading,
            fundedBillingDialog,
            showFundedBillingDialog: () => {
                fundedBillingDialog.value = true;
            },
            showBloodBillingDialog: () => {
                bloodBillingDialog.value = true;
            },
            assignCycle: () => {
                emitter.emit("daily-report-report:assign-cycle");
            },
            deassignCycle,
            viewCycleMonitoring: () => {
                emitter.emit("daily-report-report:view-cycle-monitoring");
            },
            openPdfReport,
            isStyleRed,
            openEncounter
        };
    }
};
