import { computed, inject, reactive, ref } from "vue";
import { required } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { useStore } from "vuex";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";
import { useToast } from "primevue/usetoast";
import { useRouter } from "vue-router";
import { buildPdfUrl } from "@/service/utils";
import { PROVIDER_TYPE } from "@/constants";

const THRESHOLD = {
    E2: 8000.0,
    LH: 10.0,
    PROG: 2.0,
    FSH: 10.0,
    TSH: 2.5,
    PRL: 35.0,
    DHEAS: 300.0,
    BHCG: 5.0
};

export function useDailryReportFormInit() {
    const store = useStore();
    const toast = useToast();
    const router = useRouter();
    const reportLoading = inject("reportLoading");

    const selectedDoctor = inject("selectedDoctor");
    const doctors = computed(() => store.state.doctor.doctors);

    const selectedLocation = inject("selectedLocation");
    const selectedLocationOpts = computed(() => {
        console.log(store.state.provider.locations);
        return store.state.provider.locations;
    });

    const form = inject("reportForm");

    const rules = {
        fromDate: { required },
        toDate: { required }
    };

    const v$ = useVuelidate(rules, form);
    const submitted = ref(false);

    const getOrders = () => {
        submitted.value = true;
        if (v$.value.$invalid) return;

        reportLoading.value = true;
        store
            .dispatch("dailyReport/getOrderList", {
                fromDate: format(form.fromDate, DATE_FORMAT_STRING),
                toDate: format(form.toDate, DATE_FORMAT_STRING),
                providerNo: store.state.artCycle.selectedCycle.patientId
            })
            .then((resp) => {})
            .finally(() => {
                reportLoading.value = false;
                submitted.value = false;
            });
    };

    const showSelectRowToast = () => {
        toast.add({
            severity: "warn",
            summary: "Warning",
            detail: "Please Select a row first",
            life: 3000
        });
    };

    const assignCycleDialog = inject("assignCycleDialog");
    const showAssignCycleDialog = () => {
        if (!store.getters["dailyReport/isAnyReportSelected"]) {
            showSelectRowToast();
            return;
        }

        assignCycleDialog.value = true;
    };

    const cycleMonitoringBtnLoading = inject("cycleMonitoringBtnLoading");
    const showCycleMonitoring = () => {
        if (!store.getters["dailyReport/isAnyReportSelected"]) {
            showSelectRowToast();
            return;
        }
        cycleMonitoringBtnLoading.value = true;

        store
            .dispatch(
                "artCycle/getCyclesDetails",
                store.state.dailyReport.selectedReport.patientId
            )
            .then(() => {
                cycleMonitoringBtnLoading.value = false;
                router.push("/cycle-monitoring");
            })
            .finally(() => {
                cycleMonitoringBtnLoading.value = false;
            });
    };

    const setDoctorSelected = () => {
        if (store.state.user.providerType === PROVIDER_TYPE.DOCTOR) {
            selectedDoctor.value = [store.state.user.providerNo];
        }
    };

    return {
        store,
        form,
        v$,
        submitted,
        getOrders,
        selectedDoctor,
        doctors,
        setDoctorSelected,
        selectedLocation,
        selectedLocationOpts,
        showAssignCycleDialog,
        showCycleMonitoring,
        cycleMonitoringBtnLoading
    };
}

export function useDailyReportReportsInit() {
    const store = useStore();
    const toast = useToast();

    const selectedDoctor = inject("selectedDoctor");
    const seletecdLocaction = inject("selectedLocation");
    const reportList = computed(() => {
        const locations = seletecdLocaction.value.map((item) => item.shortName);

        if (locations.length == 0) {
            return store.state.dailyReport.reportList.filter((item) =>
                selectedDoctor.value.includes(item.drNum)
            );
        } else {
            return store.state.dailyReport.reportList.filter((item) => {
                return (
                    selectedDoctor.value.includes(item.drNum) &&
                    locations.includes(item.location)
                );
            });
        }
    });

    const selectedReport = computed({
        get: () => store.state.dailyReport.selectedReport,
        set: (val) => {
            store.commit("dailyReport/SET_SELECTED_REPORT", val);
        }
    });
    const reportLoading = inject("reportLoading");
    const cycleMonitoringBtnLoading = inject("cycleMonitoringBtnLoading");

    const checkedOneOnChange = (data) => {
        saveCheckedSigleRecord({
            key: data.autoTestOrderId,
            value: data.checked,
            checknum: 1
        });
    };

    const checkedTowOnChange = (data) => {
        saveCheckedSigleRecord({
            key: data.autoTestOrderId,
            value: data.checked2,
            checknum: 2
        });
    };

    const saveCheckedSigleRecord = (payload) => {
        store
            .dispatch("dailyReport/saveCheckedSigleRecord", {
                ...payload,
                currentUser: "admin"
            })
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "Success",
                    detail: "Update Successfully",
                    life: 3000
                });
            });
    };

    const fundedBillingDialog = inject("fundedBillingDialog");
    const bloodBillingDialog = inject("bloodBillingDialog");
    const reportForm = inject("reportForm");

    const openPdfReport = () => {
        const params = {
            fromDate: format(reportForm.fromDate, DATE_FORMAT_STRING),
            toDate: format(reportForm.toDate, DATE_FORMAT_STRING),
            apiToken: store.state.user.accessToken
        };

        let url =
            "http://tablet.nlf.clinic:3280/cycles-reports/print-daily-pdf-reports.php?" +
            `fromDate=${params.fromDate}&toDate=${params.toDate}&apiToken=${params.apiToken}`;
        window.open(url);
    };

    const isStyleRed = (key, value) => {
        if (Number(value) > THRESHOLD[key]) {
            return true;
        } else {
            return false;
        }
    };

    const openEncounter = () => {
        if (selectedReport.value == null) {
            console.log("select");
        }
        const { doctorName, drNum, patientId } = { ...selectedReport.value };
        const url =
            "https://172.16.0.4/oscar/oscarEncounter/IncomingEncounter.do?" +
            `providerNo=${drNum}&providerName=${encodeURIComponent(
                doctorName
            )}&demographicNo=${patientId}`;
        window.open(url);
    };

    const deassignCycle = () => {
        store.dispatch(
            "dailyReport/deassignCycleToBloodTest",
            selectedReport.value.autoTestOrderId
        );
    };

    return {
        reportList,
        reportLoading,
        selectedReport,
        checkedOneOnChange,
        checkedTowOnChange,
        cycleMonitoringBtnLoading,
        fundedBillingDialog,
        bloodBillingDialog,
        openPdfReport,
        isStyleRed,
        openEncounter,
        deassignCycle
    };
}

export function assignCycleDialogInit() {
    const store = useStore();
    const toast = useToast();
    const assignCycleDialog = inject("assignCycleDialog");

    const cycleListLoading = ref(false);
    const cycleList = ref([]);
    const selectedCycle = ref();
    const isSelected = computed(() => selectedCycle.value != null);

    const getCycleList = () => {
        cycleListLoading.value = true;
        store
            .dispatch("dailyReport/getPersonalCycles")
            .then((data) => {
                cycleList.value = data;
            })
            .finally(() => {
                cycleListLoading.value = false;
            });
    };

    const assignCycleLoading = ref(false);
    const assignCycle = () => {
        assignCycleLoading.value = true;
        store
            .dispatch(
                "dailyReport/assignCycleToBloodTest",
                selectedCycle.value.cycleId
            )
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "Success",
                    detail: "Cycle Assigned Successfully",
                    life: 3000
                });
            })
            .finally(() => {
                assignCycleLoading.value = false;
                assignCycleDialog.value = false;
            });
    };

    return {
        assignCycleDialog,
        cycleList,
        cycleListLoading,
        selectedCycle,
        getCycleList,
        isSelected,
        assignCycle,
        assignCycleLoading
    };
}

export function fundedBillingDialogInit() {
    const store = useStore();
    const fundedBillingDialog = inject("fundedBillingDialog");

    const form = reactive({
        fromDate: null,
        toDate: null,
        patientId: null,
        cycleId: null
    });

    const dataLoading = ref(false);
    const getFundedBloodBillingList = () => {
        dataLoading.value = true;
        store
            .dispatch("dailyReport/getFundedBloodBillingList", {
                ...form,
                fromDate: form.fromDate
                    ? format(form.fromDate, DATE_FORMAT_STRING)
                    : null,
                toDate: form.toDate
                    ? format(form.toDate, DATE_FORMAT_STRING)
                    : null
            })
            .finally(() => (dataLoading.value = false));
    };

    return {
        fundedBillingDialog,
        form,
        getFundedBloodBillingList,
        dataLoading
    };
}

export function bloddBillingDialogInit() {
    const store = useStore();
    const bloodBillingDialog = inject("bloodBillingDialog");

    const form = reactive({
        fromDate: null,
        toDate: null
    });

    const rules = {
        fromDate: { required },
        toDate: { required }
    };

    const v$ = useVuelidate(rules, form);
    const submitted = ref(false);

    const dataLoading = ref(false);
    const getBloodBillingList = () => {
        submitted.value = true;
        if (v$.value.$invalid) return;

        dataLoading.value = true;
        store
            .dispatch("dailyReport/getBloodBillingList", {
                fromDate: form.fromDate
                    ? format(form.fromDate, DATE_FORMAT_STRING)
                    : null,
                toDate: form.toDate
                    ? format(form.toDate, DATE_FORMAT_STRING)
                    : null
            })
            .finally(() => {
                dataLoading.value = false;
                submitted.value = false;
            });
    };

    return {
        bloodBillingDialog,
        form,
        v$,
        submitted,
        getBloodBillingList,
        dataLoading
    };
}
