<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-formgrid p-grid p-input-filled">
                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button
                        label="Assign Cycle"
                        @click="showAssignCycleDialog"
                    />
                </div>
                <div class="p-field p-col-12 p-md-1">
                    <label class="p-mb-1">From Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.fromDate"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.fromDate.$invalid
                        }"
                    />
                </div>
                <div class="p-field p-col-12 p-md-1">
                    <label class="p-mb-1">To Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.toDate"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.toDate.$invalid
                        }"
                    />
                </div>
                <div class="p-field p-col-6 p-md-1">
                    <label class="p-mb-1">-</label>
                    <p-button label="Get Orders" @click="getOrders" />
                </div>
                <!-- <div class="p-field p-col-6 p-md-2">
                    <label class="p-mb-1">-</label>
                    <p-button
                        label="View Cycle Monitoring"
                        :loading="cycleMonitoringBtnLoading"
                        @click="showCycleMonitoring"
                    />
                </div> -->
                <div class="p-field p-col-6 p-md-2">
                    <label class="p-mb-1">Doctors</label>
                    <p-multi-select
                        v-model="selectedDoctor"
                        :options="doctors"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div class="p-field p-col-6 p-md-2">
                    <label class="p-mb-1">Location</label>
                    <p-multi-select
                        v-model="selectedLocation"
                        :options="selectedLocationOpts"
                        optionLabel="name"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { useDailryReportFormInit } from "@/composable/daily-report";
import { onMounted, onUnmounted } from "vue";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const {
            store,
            form,
            v$,
            submitted,
            getOrders,
            doctors,
            setDoctorSelected,
            selectedDoctor,
            selectedLocation,
            selectedLocationOpts,
            showAssignCycleDialog,
            showCycleMonitoring,
            cycleMonitoringBtnLoading
        } = useDailryReportFormInit();

        onMounted(() => {
            store.dispatch("doctor/getDoctorList");
            store.dispatch("provider/getProviderLocations");

            setDoctorSelected();

            emitter.on(
                "daily-report-report:assign-cycle",
                showAssignCycleDialog
            );
            emitter.on(
                "daily-report-report:view-cycle-monitoring",
                showCycleMonitoring
            );
        });

        onUnmounted(() => {
            emitter.off(
                "daily-report-report:assign-cycle",
                showAssignCycleDialog
            );
        });

        return {
            form,
            v$,
            submitted,
            getOrders,
            doctors,
            selectedDoctor,
            selectedLocation,
            selectedLocationOpts,
            showAssignCycleDialog,
            showCycleMonitoring,
            cycleMonitoringBtnLoading
        };
    }
};
</script>

<style scoped>
.p-calendar {
    width: 110px;
}
</style>
